.App {
  text-align: center;
}

div {
  box-sizing: border-box;
}

.list {
  padding: 16px 20px;
  padding-bottom: 0;
  text-align: left;
  border-bottom: 1px solid gray;
}

.list h4 {
  display: inline;
}

.list h4,
.list span {
  cursor: pointer;
}

.list__delete  {
  margin-left: 10px;
}

.black-nav {
  display: flex;
  background: black;
  width: 100%;
  color: white;
  padding-left: 20px;
}

.modal {
  margin: 20px 0;
  /* margin-left: 0; */
  padding: 20px;
  background: #ddd;
  text-align: left;
}
